import Button from "@mui/material/Button";
import Box from "@mui/material/Box";

function FillterBtns({ allClick, checkClick, nonCheckedClick }) {
  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        "& > *": {
          m: 1,
        },
        direction: "rtl",
      }}
    >
      <Button
        sx={{ margin: "5px", color: "#909090", borderColor: "#909090" }}
        variant="outlined"
        onClick={() => {
          allClick();
        }}
      >
        
      </Button>
      <Button
        color="success"
        sx={{
          margin: "5px",
        }}
        variant="outlined"
        onClick={() => {
          checkClick();
        }}
      >
        
      </Button>
      <Button
        color="error"
        sx={{
          margin: "5px",
        }}
        variant="outlined"
        onClick={() => {
          nonCheckedClick();
        }}
      >
         
      </Button>
    </Box>
  );
}

export default FillterBtns;
