
function PopUp({ text, color }) {
  return (
    <span
      style={{
        textAlign: "center",
        color: "#fff",
        backgroundColor: color,
        padding: "5px 20px",
        borderRadius: "3px",
        position: "absolute",
        bottom: "10px",
        left: "10px",
      }}
    >
      {text}
    </span>
  );
}

export default PopUp;
