import { createTheme, ThemeProvider } from "@mui/material";
import "./App.css";
import FillterBtns from "./FiltterBtns";
import Tasks from "./Tasks";
import Add from "./Add";
import PopUp from "./PopUp";
import { useEffect, useState } from "react";
import EditPop from "./EditPop";
import { data } from "react-router-dom";

function App() {
  const theme = createTheme({
    typography: {
      fontFamily: "CairoBlack",
    },
    paconstte: {
      success: { main: "#4caf50" },
      error: { main: "#f44336" },
    },
  });

  useEffect(() => {
    const savedItems = localStorage.getItem("items");
    if (savedItems) {
      setItem(JSON.parse(savedItems));
    }
  }, []);

  const [items, setItem] = useState([]);
  const [eidt, setEdit] = useState(false);
  const [pop, setPop] = useState(false);
  const [popTxt, setPopTxt] = useState("");
  const [popColor, setPopColor] = useState(theme.paconstte.success.main);
  const [eItem, setEItem] = useState("");
  const [filter, setFilter] = useState("all");
  function popup(color, text) {
    setPopColor(color);
    setPop(true);
    setPopTxt(text);
    setTimeout(() => {
      setPop(false);
    }, 5000);
  }

  const filteredItems = items.filter((item) => {
    if (filter === "all") return true;
    if (filter === "checked") return item.checked;
    if (filter === "nonChecked") return !item.checked;
  });
  return (
    <ThemeProvider theme={theme}>
      <div
        style={{
          backgroundColor: "#121415",
          width: "100%",
          height: "100vh",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          direction: "rtl",
        }}
      >
        {eidt && (
          <EditPop
            onClick={(title, descrip) => {
              const newItems = items.map((a) => {
                if (a.id !== eItem) {
                  return a;
                } else {
                  return { ...a, id: eItem, title: title, descrip: descrip };
                }
              });

              setItem(newItems);
              localStorage.setItem("items", JSON.stringify(newItems));
              setEdit(false);
              popup("rgb(25, 118, 210)", "  ");
            }}
            close={() => {
              setEdit(false);
            }}
          />
        )}
        <div
          style={{
            width: "500px",
            padding: "15px",
            backgroundColor: "white",
            boxShadow: " 0 0 50px -20px #fefefe",
            borderRadius: "15px",
            maxHeight: "90vh",
            position: "relative",
          }}
        >
          <h1
            style={{
              color: "black",
              borderBottom: "2px solid #eee",
              paddingBottom: "15px",
              textAlign: "center",
              fontWeight: "bold",
            }}
          >
            
          </h1>
          <FillterBtns
            allClick={() => setFilter("all")}
            checkClick={() => setFilter("checked")}
            nonCheckedClick={() => setFilter("nonChecked")}
          />
          <div
            style={{
              marginTop: "10px",
              overflowY: "scroll",
              height: "50vh",
              position: "relative",
              marginBottom: "10px",
            }}
          >
            {filteredItems.map((e) => {
              return (
                <Tasks
                  key={e.id}
                  taskName={e.title}
                  descrip={e.descrip}
                  checked={e.checked}
                  delClick={() => {
                    const newItems = items.filter((a) => a.id !== e.id);
                    setItem(newItems);
                    localStorage.setItem("items", JSON.stringify(newItems));
                    popup(theme.paconstte.error.main, "  ");
                  }}
                  editClick={() => {
                    setEItem(e.id);
                    setEdit(true);
                  }}
                  checkClick={() => {
                    const newItems = items.map((a) => {
                      if (a.id !== e.id) return a;
                      return { ...a, checked: !a.checked };
                    });
                    setItem(newItems);
                    localStorage.setItem("items", JSON.stringify(newItems));
                  }}
                />
              );
            })}
          </div>
          <div>
            <Add
              onClick={(title) => {
                const newItems = [
                  ...items,
                  {
                    id:
                      Date.now().toString(36) +
                      "-" +
                      Math.random().toString(36),
                    title: title,
                    descrip: "",
                    checked: false,
                  },
                ];
                setItem(newItems);
                localStorage.setItem("items", JSON.stringify(newItems));
                popup("rgb(25, 118, 210)", "  ");
              }}
            />
          </div>
        </div>
        {pop && <PopUp text={popTxt} color={popColor} />}
      </div>
    </ThemeProvider>
  );
}

export default App;
