import { Button, TextField } from "@mui/material";
import { useState } from "react";

function EditPop({ onClick, close }) {
  let [title, setTitle] = useState("");
  let [descrip, setDescrip] = useState("");
  return (
    <div
      style={{
        position: "absolute",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        zIndex: 1000,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <div
        style={{
          width: "100%",
          height: "100%",
          backgroundColor: "#000000",
          position: "absolute",
          top: 0,
          left: 0,
          zIndex: 1000,
          opacity: 0.5,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
          color: "#ffffff",
          fontSize: "20px",
          fontWeight: "bold",
        }}
        onClick={() => close()}
      ></div>
      <div
        style={{
          width: "500px",
          backgroundColor: "#ffffff",
          borderRadius: "10px",
          padding: "20px",
          zIndex: 1002,
          position: "relative",
        }}
      >
        <TextField
          style={{ width: "100%" }}
          id="outlined-basic"
          label=""
          variant="outlined"
          value={title}
          onChange={(e) => {
            setTitle(e.target.value);
          }}
        />
        <TextField
          style={{ width: "100%", margin: "20px 0" }}
          id="outlined-basic"
          label=""
          variant="outlined"
          value={descrip}
          onChange={(e) => {
            setDescrip(e.target.value);
          }}
        />
        <Button variant="contained" onClick={() => onClick(title, descrip)}>
          
        </Button>
      </div>
    </div>
  );
}
export default EditPop;
