import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import CheckIcon from "@mui/icons-material/Check";
import { IconButton } from "@mui/material";
import { useTheme } from "@emotion/react";

function Tasks({
  taskName,
  descrip,
  delClick,
  editClick,
  checkClick,
  checked,
}) {
  const theme = useTheme();

  const colorback = checked ? theme.palette.success.main : "white";
  const colorCheck = checked ? "white" : theme.palette.success.main;

  return (
    <div
      style={{
        borderRadius: "6px",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        padding: "10px",
        backgroundColor: "#bfbfbf",
        boxShadow: "0px 0px 10px -5px #000",
        marginBottom: "10px",
        fontFamily: "CairoBlack",
      }}
    >
      <div style={{ flexBasis: "70%" }}>
        <h2>{taskName}</h2>
        <p>{descrip}</p>
      </div>
      <div
        style={{
          flexBasis: "30%",
          display: "flex",
          alignItems: "center",
          justifyContent: "space-evenly",
        }}
      >
        <IconButton
          color="success"
          sx={{
            padding: "5px",
            border: `2px solid ${theme.palette.success.main}`,
            backgroundColor: colorback,
            "&:hover": {
              backgroundColor: colorback,
              boxShadow: "0px 0px 10px -5px #000",
            },
            transition: "all 0.5s",
          }}
          onClick={checkClick}
        >
          <CheckIcon style={{ color: colorCheck }} />
        </IconButton>
        <IconButton
          color="primary"
          sx={{
            padding: "5px",
            border: "2px solid currentColor",
            backgroundColor: "white",
            "&:hover": {
              backgroundColor: "white",
              boxShadow: "0px 0px 10px -5px #000",
            },
            transition: "all 0.5s",
          }}
          onClick={editClick}
        >
          <EditIcon />
        </IconButton>
        <IconButton
          color="error"
          sx={{
            padding: "5px",
            border: `2px solid ${theme.palette.error.main}`,
            backgroundColor: "white",
            "&:hover": {
              backgroundColor: "white",
              boxShadow: "0px 0px 10px -5px #000",
            },
            transition: "all 0.5s",
          }}
          onClick={delClick}
        >
          <DeleteIcon />
        </IconButton>
      </div>
    </div>
  );
}

export default Tasks;
