import Button from "@mui/material/Button";
import { TextField } from "@mui/material";
import { useState } from "react";

function Add({ onClick }) {
  let [title, setTitle] = useState("");
  return (
    <div style={{ display: "flex", gap: "20px" }}>
      <TextField
        style={{ flexGrow: "1" }}
        id="outlined-basic"
        label=" "
        variant="outlined"
        size="small"
        value={title}
        onChange={(e) => {
          setTitle(e.target.value);
        }}
      />
      <Button
        onClick={() => {
          if (title !== "") {
            onClick(title);
            setTitle("");
          }
        }}
        variant="contained"
      >
         
      </Button>
    </div>
  );
}

export default Add;
